.Icon-container {
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 48px;
    width: 48px;
    font-size: 24px;
    font-weight: bold;
    /* background-color: #CCC; */
  }

  
  .Icon-container-check {
    position: absolute;
    left: 12px;
  }
  .Icon-container-check--active {
    color: #fafafa;
    background-color: #3fca40;
    border-radius: 50%;
  }
  
  .Icon-container-delete {
    position: absolute;
    top: -24px;
    right: 0;
  }
  .Icon-container-delete:hover {
    color: red;
  }
  .Icon-svg{
    width: 24px;
    height: 24px;
    
  }


 .Icon-container-delete:hover  .Icon-svg{
    fill: red;
  }
  
  .Icon-container-check:hover  .Icon-svg{
    fill: green;
  }

  .Icon-container-empty .Icon-svg{
   
    width: 50px;
    height: 50px !important;

  }
  .Icon-container-empty{
   
margin: auto;

  }